import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export const store = new Vuex.Store({
  state: {
    videoDetail: [],
    load: false,
    isComplete: false
  },
  mutations: {
    setVideoData(state, payload) {
        state.videoDetail.push(payload)
    },
    resetVideoDetail(state) {
        state.videoDetail.shift()
    },
    startLoad(state){ 
        state.load = true
    },
    endLoad(state) {
        state.load = false
    },
    loadComplete(state) {
        state.isComplete = true
    }
  },
  actions: {
      setVideoData(context, payload) {
          context.commit('setVideoData', payload)
      },
      startLoad(context) {
          context.commit('resetVideoDetail')
          context.commit('startLoad')
      },
      endLoad(context) {
        context.commit('endLoad')
    },
    loadComplete(context) {
        context.commit('loadComplete')
    }
  },
  getters: {
      getVideoData(state) {
          return state.videoDetail
      },
      getLoad(state) {
          return state.load
      },
      loadComplete(state) {
          return state.isComplete
      }
  }
})