<template>
  <div class="inner__block">
    <div class="inner">
      <div class="inner__title">
        <p>enter the link in the input field</p>
      </div>
      <div class="inner__body" :class="{inner__body_shadow : url}">
        <div class="inner__input">
          <input type="url" placeholder="https://youtube.com/" v-model="url" />
        </div>
        <div class="inner__btn">
          <div class="close__section" v-show="url">
            <button>
              <span class="material-icons" title="clear" @click="clearUrl"
                >close</span
              >
            </button>
            <span>|</span>
          </div>
          <button>
            <span
              class="material-icons"
              title="search"
              :style="searchColor"
              @click="getVideoData"
              >search</span
            >
          </button>
        </div>
      </div>
    </div>
  </div>
</template>


<script>
export default {
  data() {
    return {
      url: "",
      title: "",
      thumbnails: [],
    };
  },
  methods: {
    getVideoData() {
      if (this.url) {
        this.$store.dispatch('startLoad')

        fetch("http://localhost:2020/", {
          method: "POST",
          headers: {
            "Content-Type": "application/json",
          },
          body: JSON.stringify({
            url: this.url,
          }),
        })
          .then((response) => response.json())
          .then((data) => this.setDataToVuex(data));
      }
    },
    setDataToVuex(data) {
      this.$store.dispatch("setVideoData", data);
      this.$store.dispatch('endLoad')
      this.$store.dispatch('loadComplete')
    },
    clearUrl() {
      this.url = "";
    },
  },
  watch: {
    videoInfo() {},
  },
  computed: {
    searchColor() {
      return this.url ? `color:#4285f4;` : "color:#9aa0a6;";
    },
  },
};
</script>

<style>
@import url("https://fonts.googleapis.com/css2?family=Lato:wght@300;400;700&display=swap");

.inner__block {
  width: 550px;
  margin: 0 auto;
  text-align: left;
}

.inner__block p {
  font-family: "Lato", sans-serif;
  font-size: 17px;
  text-transform: capitalize;
  padding: 0 15px;
}

.inner__body {
  background: #fff;
  width: 100%;
  display: flex;
  border: 1px solid #dfe1e5;
  height: 35px;
  align-items: center;
  border-radius: 25px;
}

.inner__body:hover {
      box-shadow: 0 2px 5px 1px rgb(64 60 67 / 16%);
}

.inner__body_shadow {
      box-shadow: 0 2px 5px 1px rgb(64 60 67 / 16%);
}

.inner__input {
  width: 80%;
}

input[type="url"] {
  width: 100%;
  padding: 5px 20px;
  font-size: 16px;
  background-color: transparent;
  border: none;
  outline: none;
}

.inner__btn {
  width: 20%;
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.close__section {
  display: flex;
  justify-content: center;
  align-items: center;
}

.inner__btn button {
  background: none;
  border: none;
  margin-top: 5px;
}

button {
  cursor: pointer;
}

.inner__btn span {
  color: #9aa0a6;
}
</style>