<template>
  <div class="outer__section">
    <a :href="audioUrl" ref="outer" download></a>
    <div class="loading__block">
      <div class="lds-default" v-if="loading">
        <div></div>
        <div></div>
        <div></div>
        <div></div>
        <div></div>
        <div></div>
        <div></div>
        <div></div>
        <div></div>
        <div></div>
        <div></div>
        <div></div>
      </div>
    </div>

    <div class="outer__block" :class="{download__music : startDownloadAudio}" v-if="isComplete">
      <div class="outer__image">
        <img :src="videoThumbnail" alt="" />
      </div>
      <div class="outer__info">
        <div class="outer__title">
          <h3>{{ title }}</h3>
        </div>
        <div class="outer__btns">
          <button><a :href="loadVideoUrl" target="blank">video</a></button>
          <button @click="downloadMusic">audio</button>
        </div>
      </div>
    </div>

    
      <div class="audio__load__animation" v-if="startDownloadAudio">
        <div class="lds-ellipsis"><div></div><div></div><div></div><div></div></div>
        <p>Please wait, the file is being compressed</p>
      </div>
  </div>
</template>


<script>
export default {
  data() {
    return {
      videoInfo: this.$store.getters.getVideoData,
      title: "",
      loadVideoUrl: "",
      thumbnails: [],
      url: '',
      startDownloadAudio: false
    };
  },
  methods: {
    downloadMusic() {
      console.log('Fetch Music...')
      this.startDownloadAudio = true

      fetch('http://localhost:2020/music/', {
        method: "POST",
        headers: {
          'Content-Type': 'application/json'
        },
        body: JSON.stringify({
          url: this.url,
          title: this.title
        })
      }).then(res => res.json())
      .then(data => {
        console.log(data)
        this.downloadToPc()
        this.startDownloadAudio = false
      })
    },
    downloadToPc() {
      let link = this.$refs.outer
      link.click()
    }
  },
  computed: {
    loading() {
      return this.$store.getters.getLoad;
    },
    isComplete() {
      return this.$store.getters.loadComplete
    },
    videoThumbnail() {
      return this.thumbnails[this.thumbnails.length - 1].url;
    },
    audioUrl() {
      return this.title ? `download/${this.title}.mp3` : ''
    }
  },
  watch: {
    videoInfo() {
      if (this.videoInfo.length > 0) {
        let data = this.videoInfo[0];

        this.title = data.info.videoDetails.title;
        this.thumbnails = data.info.videoDetails.thumbnails;
        this.loadVideoUrl = data.video.url;
        this.url = data.info.videoDetails.video_url
      }
    },
  },
};
</script>



<style>
@import "../../assets/load.css";
@import "../../assets/audioLoad.css";
@import "../../assets/mobile.css";

.outer__block {
  width: 600px;
  height: 300px;
  padding: 30px;
  margin: 0 auto;
  display: flex;
  align-items: flex-start;
}
.outer__image {
  width: 40%;
  height: 50%;
}

.outer__image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.outer__info {
  width: 60%;
}

.outer__title {
  font-size: 22px;
  margin-top: -25px;
}

.loading__block {
  position: absolute;
  right: 0;
  left: 0;
  margin: 0 auto;
}

.outer__btns a {
  text-decoration: none;
  color: #2c3e50;
}

.outer__section {
    position: relative;
}

.audio__load__animation {
    position: absolute;
    left: 0;
    right: 0;
    top: 40px;
}

.audio__load__animation p {
    margin: -10px;
}

.download__music {
  opacity: .4;
}
</style>